.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

nav {
  display: flex;
  padding: 10px;
  background: #fff;
  box-shadow: 0 0 3px 2px #ccc;
}

nav li {
  list-style: none;
  padding: 10px;
  margin-right: 10px;
  cursor: pointer;
}

nav li:hover {
  border-radius: 10px;
  background: #f1f1f1;
  transition: .5s ease-in-out;
}

.class1 {
  font-size: large;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}